export default {
	data() {
		return {
			showTitle:false,
			guodu:[],
			scrollId:'',
			TotalLable:[],
			// defaultLable:[],
			// addLable:[],
			n:0,
			page:1,
			cate_name:'',
			loading:false,
			allowloading:false,
			hotList:[] //热门项目
		}
	},
	onPullDownRefresh(){
		this.page = 1
		this.getData()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	onReachBottom(){
		if(this.allowloading){
			this.allowloading = false
			this.loading = true
			this.page++
			this.getData()
			// setTimeout(() => {
			// 	this.page++
			// 	this.getData()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	onLoad() {
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		this.getLable()
	},
	onShow() {
		this.getAddLable()
	},
	methods: {
		goTab(){
			uni.navigateTo({
				url:'../../pagesTwo/TabSelectNew/TabSelectNew'
			})
		},
		tab(ind,it){
			this.scrollId = 'id'+ind
			this.n = ind
			this.cate_name = it
			this.getData()
		},
		// 会友汇详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		// 跳转个人主页
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 获取列表
		getData(){
		      this.$api.opportunity_list({
		              token: uni.getStorageSync("token"),
		              user_id: uni.getStorageSync("userId"),
		              page: this.page,
					  cate_name : this.cate_name
		            }).then(res => {
						if(res.data.code=== '200'){
							if(this.page===1){
								uni.hideLoading();
								this.hotList = res.data.data
								if(res.data.data.length>=20){
									this.allowloading= true
									this.loading =false
								}else{
									this.allowloading= false
									this.loading =false
									this.showTitle = true
								}
							}else{
								this.hotList = this.hotList.concat(res.data.data)
								if(res.data.data.length>=20){
									this.allowloading= true
									this.loading =false
								}else{
									this.allowloading= false
									this.loading =false
									this.showTitle = true
								}
							}
							
						}
		            })
				
		},
		// 获取自己添加的标签
		getAddLable(){
			this.$api.myself_label({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
			}).then(res=>{
				
				// this.addLable = res.data.data
				this.TotalLable = this.guodu.concat(res.data.data)
				
				this.cate_name = this.TotalLable[this.n]
				// console.log(this.n)
				// console.log(res.data.data)
				this.getData()
				
			})
		},	
		// 获取默认选项
		getLable(){
			this.$api.default_label({
			}).then(res => {
				// this.defaultLable = res.data.data
				this.guodu = res.data.data
				this.TotalLable = this.TotalLable.concat(res.data.data)
				this.$nextTick(()=>{
					this.getAddLable()
				})
				
			})
		}
	}
}
